import Box from "@mui/material/Box";
import Chip from "@mui/material/Chip";
import Paper from "@mui/material/Paper";

import { styles } from "./bottom-image-card.styles";

interface bottomImageCardProps {
  /**
   * id: String thats needs to be displayed in chip
   */
  id?: string;
  children: JSX.Element | JSX.Element[];
}

/**
 * Paper element with chip at top-left
 * and render children within paper
 */
export const BottomImageCard = ({ id, children }: bottomImageCardProps) => {
  return (
    <Paper elevation={2} sx={{ m: 2 }}>
      <Box sx={styles.wrapper}>
        {id && <Chip label={id} color="primary" sx={styles.chip} />}
      </Box>
      <Box sx={styles.center}>{children}</Box>
      <Box sx={styles.bottomSpace} />
    </Paper>
  );
};
