import { useMutation, useQuery } from "@tanstack/react-query";
import { AxiosError, AxiosResponse } from "axios";
import { useCallback, useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Chip from "@mui/material/Chip";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";

import { AlertContext } from "../../contexts/alert";
import { GlobalsContext } from "../../contexts/globals";
import {
  DeliveryDocument,
  InstructionResponse,
  PrintJob,
} from "../../types/instruction";
import { ScannedDataListitem } from "../../types/instruction";
import { MyAppsData } from "../../types/my-apps";
import { PrintRequestResponse } from "../../types/printing";
import {
  apiHandler,
  GetReceiveInstructionRequest,
} from "../../utilities/api-handler";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { axiosRequest } from "../../utilities/axios-request";
import { applicationIdentifier } from "../../utilities/global-scanner/utilities/application-identifier";
import { printingHandler } from "../../utilities/printing/printing-handler";
import { useLazyPrint } from "../../utilities/printing/useLazyPrint";
import { sessionHandler } from "../../utilities/session-handler";
import { useScannedEventListener } from "../../utilities/useEventListener";
import { useHandlePageRefresh } from "../../utilities/useHandlePageRefresh";
import { BottomImageCard } from "../common/bottom-image-card/bottom-image-card";
import { CustomConfirmDialogBox } from "../common/dialog-box/dialog-confim";
import { Barcode } from "../common/images/barcode";
import { Scan2DBarcode } from "../common/images/scan-2d-barcode";
import { SubHeader } from "../common/sub-header/sub-header";
import { ScanDockTagOrTclNavState } from "../dock-tag/dock-tag";
import { useGlobalKeyboard } from "../global-keyboard/useGlobalKeyboard";
import { RegulatedItemModal } from "../regulated-item/regulated-item-modal";
import { allowedStatusHandler } from "./allowed-status";
import { styles } from "./scan-upc.styles";

const postScannedUpcInfo = (
  receiveInstructionRequest: GetReceiveInstructionRequest
) =>
  axiosRequest(apiHandler.getReceiveInstruction(receiveInstructionRequest), {
    description: `Scan-Upc: get upc ${receiveInstructionRequest.upcNumber} details`,
  });

const getScannedUpcInfo = (deliveryNumber: string, upcNumber: string) =>
  axiosRequest(apiHandler.getScannedUpcInfo({ deliveryNumber, upcNumber }), {
    description: `Scan-Upc: get upc ${upcNumber} details`,
  });

export interface ScanUpcNavState extends ScanDockTagOrTclNavState {
  gdmDeliveryDocuments: DeliveryDocument[];
  scannedUpc: string;
  itemQuantity?: string;
  receivingType?: string;
  scannedDataList?: ScannedDataListitem[] | null;
  trackingId?: string;
  printJob?: PrintJob;
  isTclFreeFlow?: boolean;
}

/**
 *
 * @route Fetches item upc details
 * @description
 *  if matched with mutiple POs -> PO-Selection route
 *
 *  else -> Item-Quantity route
 */
export default function ScanUpc() {
  // get state values from scan-dock-tag page
  const scanDockTagOrTclNavState =
    useHandlePageRefresh<ScanDockTagOrTclNavState>();
  const navigate = useNavigate();
  const [upc, setUpc] = useState("");
  const [deliveryNumber, setDeliveryNumber] = useState("0");
  const [errorCode, setErrorCode] = useState("");
  const [isTclFreeFlow, setIsTclFreeFlow] = useState(false);
  const [parsed2dMap, setParsed2dMap] = useState(new Map<string, string>());
  const [deliveryDoc, setDeliveryDoc] = useState<DeliveryDocument[] | null>(
    null
  );
  const [openRegulatedModal, setopenRegulatedModal] = useState(false);
  const [openLabelPrintSuccess, setopenLabelPrintSuccess] = useState(false);

  const { setAlerts } = useContext(AlertContext);
  const { globals, setGlobals } = useContext(GlobalsContext);
  const [header, setHeader] = useState(<></>);
  const [content, setContent] = useState(<></>);

  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const enableDockTag = myAppsData?.dynamicConfig.enableDockTag || false;
  const enableRegulatedItemChange =
    myAppsData?.dynamicConfig.enableRegulatedItemChange || false;
  const enableQtyCorrection =
    myAppsData?.dynamicConfig.enableQtyCorrection || false;
  const enableSkipQtyPage =
    myAppsData?.dynamicConfig.enableSkipQtyPage || false;
  const enableIsAuditRequired =
    myAppsData?.dynamicConfig.enableIsAuditRequired || false;
  const enableNewTCLRoute =
    myAppsData?.dynamicConfig.enableNewTCLRoute || false;
  const isTCLRouteFlow =
    enableNewTCLRoute && scanDockTagOrTclNavState?.isTCLRoute;
  useEffect(() => {
    // reset multi sku if on root page or scan-dock-tag page
    setGlobals({
      ...globals,
      multiSku: false,
    });
    const UPC = `${scanDockTagOrTclNavState?.dockScanUpc}`;
    const PO = `${scanDockTagOrTclNavState?.dockScanUpc}`;
    const parsed2DMap = scanDockTagOrTclNavState?.parsed2DMap;
    let barcodeData = parsed2DMap?.get("01");
    if(isTCLRouteFlow && parsed2DMap && barcodeData){
      setParsed2dMap(parsed2DMap);
      onBarcodeScanned(barcodeData);
    }
    if (enableNewTCLRoute && PO !== undefined && (!parsed2DMap || !parsed2DMap.size)) {
      setDeliveryNumber(PO);
    }
    if (enableNewTCLRoute && UPC !== "undefined" && (!parsed2DMap || !parsed2DMap.size)) {
      setIsTclFreeFlow(true);
      onBarcodeScanned(`${scanDockTagOrTclNavState?.dockScanUpc}`);
    }
    if(scanDockTagOrTclNavState?.deliveryDocuments!==undefined) {
      getScannedUpcInfoSuccess(scanDockTagOrTclNavState?.deliveryDocuments);
    }
    // eslint-disable-next-line
  }, []);

  const getScannedUpcInfoSuccessViaGdm = (response: AxiosResponse) => {
    setAlerts({ success: "UPC found" });

    const allowedDeliveryDocs = allowedStatusHandler.filterDeliveryDocuments(
      response.data
    );
    const navState = {
      ...scanDockTagOrTclNavState,
      gdmDeliveryDocuments: allowedDeliveryDocs,
      scannedUpc: upc,
    };

    // if respnse has multiple delivery documents, user has to slect from the list
    if (allowedDeliveryDocs.length > 1) {
      // navigate to po-selection route
      appLogger.action("Scan-Upc: navigate to po-selection page");
      navigate("/po-selection", {
        state: {
          ...navState,
          itemQuantity: parsed2dMap.get("30"),
          scannedDataList: getScannedDataList(parsed2dMap),
          receivingType: parsed2dMap.size > 0 ? "GS1" : "upc",
        },
      });
    } else if (allowedDeliveryDocs.length === 1) {
      // navigate to enter-quntity route
      appLogger.action("Scan-Upc: navigate to item-quantity page");
      navigate("/item-quantity", {
        state: {
          ...navState,
          itemQuantity: parsed2dMap.get("30"),
          isTclFreeFlow,
          scannedDataList: getScannedDataList(parsed2dMap),
          receivingType: parsed2dMap.size > 0 ? "GS1" : "upc",
        },
      });
    } else {
      setAlerts({ error: "No POs are available" });
      appLogger.error("Scan-Upc: no po's availaible for scanned upc", {});
    }
  };

  const getScannedUpcInfoErrorViaGdm = (error: AxiosError<any>) => {
    setAlerts({
      error:
        (error?.response?.data.description &&
          error?.response?.data.description[0]) ||
        error?.message ||
        "UPC error",
    });
  };

  const getScannedUpcInfoSuccess = (response: AxiosResponse) => {
    try {
      const allowedDeliveryDocs = response.data
        .deliveryDocuments as DeliveryDocument[];
      setDeliveryDoc(allowedDeliveryDocs);
      const instructionCode =
        response.data.instruction?.instructionCode || null;
      const navState = {
        ...scanDockTagOrTclNavState,
        gdmDeliveryDocuments: allowedDeliveryDocs,
        scannedUpc: upc,
      };
      if(enableNewTCLRoute) {
        navState.deliveryNumber = allowedDeliveryDocs[0].deliveryNumber;
      }
      const auditRequired = isAuditRequired(allowedDeliveryDocs);
      const receivingTier = getReceivingTier(allowedDeliveryDocs);

      // if respnse has multiple delivery documents, user has to slect from the list
      if (instructionCode === "MANUAL_PO_SELECTION") {
        // navigate to po-selection route
        setAlerts({ success: "UPC found" });
        appLogger.action("Scan-Upc: navigate to po-selection page");
        if (enableIsAuditRequired) {
          navigate("/po-selection", {
            state: {
              ...navState,
              itemQuantity:
                auditRequired === false ? parsed2dMap.get("30") : null,
              scannedDataList: getScannedDataList(parsed2dMap),
              receivingType: parsed2dMap.size > 0 ? "GS1" : "upc",
            },
          });
        } else {
          navigate("/po-selection", {
            state: {
              ...navState,
              itemQuantity:
                receivingTier && receivingTier === "HIGH"
                  ? parsed2dMap.get("30")
                  : null,
              scannedDataList: getScannedDataList(parsed2dMap),
              receivingType: parsed2dMap.size > 0 ? "GS1" : "upc",
            },
          });
        }
      } else if (instructionCode === "WFS Quantity Capture") {
        var itemQty =
          receivingTier && receivingTier === "HIGH"
            ? parsed2dMap.get("30")
            : null;

        if (enableIsAuditRequired) {
          itemQty = auditRequired === false ? parsed2dMap.get("30") : null;
        }

        if (
          enableSkipQtyPage &&
          itemQty != null &&
          allowedDeliveryDocs != null
        ) {
          // skip quantity page
          appLogger.action("Scan-Upc: trusted seller skip item-quantity page");
          receiveForTrustedSeller(allowedDeliveryDocs);
        } else {
          // navigate to enter-quntity route
          setAlerts({ success: "Scan successful" });
          appLogger.action("Scan-Upc: navigate to item-quantity page");
          navigate("/item-quantity", {
            state: {
              ...navState,
              itemQuantity: itemQty,
              isTclFreeFlow,
              scannedDataList: getScannedDataList(parsed2dMap),
              receivingType: parsed2dMap.size > 0 ? "GS1" : "upc",
            },
          });
        }
      } else {
        const isLithium =
          allowedDeliveryDocs[0].deliveryDocumentLines[0]
            ?.isLithiumIonVerificationRequired || false;
        const isLimited =
          allowedDeliveryDocs[0].deliveryDocumentLines[0]
            ?.isLimitedQtyVerificationRequired || false;
        const isRegulated = isLimited || isLithium;
        if (isRegulated && !instructionCode) {
          setAlerts({ loading: false });
          appLogger.action("Scan-Upc: checking for regulated Item");
          setopenRegulatedModal(true);
        } else {
          setAlerts({ error: "No POs are available" });
          appLogger.error("Scan-Upc: no po's availaible for scanned upc", {});
        }
      }
    } catch (error) {
      appLogger.error(
        "Scan-Upc: Unable to process success response for receving",
        {
          response,
        }
      );
    }
  };

  const isAuditRequired = (allowedDeliveryDocs: DeliveryDocument[]) => {
    if (
      allowedDeliveryDocs.length > 0 &&
      allowedDeliveryDocs[0].additionalInfo &&
      allowedDeliveryDocs[0].additionalInfo.isAuditRequired !== undefined &&
      allowedDeliveryDocs[0].additionalInfo.isAuditRequired !== null
    ) {
      return allowedDeliveryDocs[0].additionalInfo.isAuditRequired;
    }
    return true;
  };

  const getReceivingTier = (allowedDeliveryDocs: DeliveryDocument[]) => {
    if (
      allowedDeliveryDocs.length > 0 &&
      allowedDeliveryDocs[0].additionalInfo &&
      allowedDeliveryDocs[0].additionalInfo.receivingTier
    ) {
      return allowedDeliveryDocs[0].additionalInfo.receivingTier;
    }
    return null;
  };

  const getScannedUpcInfoError = (error: AxiosError<any>) => {
    setAlerts({
      error:
        error?.response?.data?.errorMessage || error?.message || "UPC error",
    });
    const errorCode = error?.response?.data?.errorCode;

    setErrorCode(errorCode);
    if (!(enableNewTCLRoute && errorCode === "TCL.201.NOT_FOUND")) {
      navigate(-1);
    }
  };

  const { mutate: receiveItems } = useMutation(postScannedUpcInfo, {
    onSuccess: (response) =>
      getScannedUpcInfoSuccess(response as AxiosResponse),
    onError: (error) => getScannedUpcInfoError(error as AxiosError<any>),
  });

  const onApproveRegulatedItemLables = useCallback(() => {
    appLogger.info("Scan-Upc: approved all regulated items are labelled", {});
    setopenRegulatedModal(false);
    const gs1 = parsed2dMap.get("gs1") && parsed2dMap.get("gs1");
    if (deliveryDoc) {
      receiveItems({
        deliveryNumber: `${scanDockTagOrTclNavState?.deliveryNumber}`,
        doorNumber: `${scanDockTagOrTclNavState?.doorNumber}`,
        upcNumber: parsed2dMap.size > 0 ? parsed2dMap.get("01") || "" : upc,
        previouslyScannedDataList: (gs1 && [gs1]) || null,
        deliveryDocuments: deliveryDoc,
        enteredQty: null,
        isMultiSKUItem: globals.multiSku,
        isVendorComplianceValidated: true,
        scannedDataList:
          parsed2dMap.size > 0 ? getScannedDataList(parsed2dMap) : null,
        receivingType: parsed2dMap.size > 0 ? "GS1" : "UPC",
      });
    }
  }, [
    receiveItems,
    setopenRegulatedModal,
    deliveryDoc,
    globals.multiSku,
    scanDockTagOrTclNavState,
    upc,
    parsed2dMap,
  ]);

  const onApprovePrintingLabelSuccess = useCallback(() => {
    appLogger.info(
      "Update-Item-Quantity: Approved for editing trusted seller quantity",
      {}
    );
    appLogger.action("Item-Quantity: navigate to scan-dock-tag page");
    setAlerts({ loading: false });
    setopenLabelPrintSuccess(false);
    navigate("/scan-dock-tag-or-tcl", {
      state: {
        cartId: scanDockTagOrTclNavState?.cartId,
        doorNumber: `${scanDockTagOrTclNavState?.doorNumber}`,
      },
    });
  }, [setAlerts, setopenLabelPrintSuccess, scanDockTagOrTclNavState]);

  const printInstructionPrintJobSuccess = useCallback(
    (response: PrintRequestResponse[]) => {
      appLogger.info("Item-Quantity: printjob success", response);
      setopenLabelPrintSuccess(true);
    },
    [setopenLabelPrintSuccess]
  );
  const printInstructionPrintJobError = useCallback(
    (error: unknown) => {
      appLogger.error("Item-Quantity: printjob error", error as object);
      setAlerts({
        error:
          "There was an error printing. Check the printer status or re-connect to continue.",
      });
    },
    [setAlerts]
  );

  const [printInstructionPrintJob] = useLazyPrint({
    onPrintRequestSuccess: printInstructionPrintJobSuccess,
    onPrintRequestError: printInstructionPrintJobError,
  });

  const receiveTrustedItemsSuccess = (
    response: AxiosResponse<InstructionResponse>
  ) => {
    // resetting previouslyScannedDataList on successful receive instruction.
    setGlobals({
      ...globals,
      previouslyScannedDataList: [],
    });
    // before printing we need to identify casepack or breakpack
    // print labels on if casepack
    const inventoryStatus =
      response.data.instruction?.container.inventoryStatus;
    const { printJob } = response.data;
    switch (inventoryStatus) {
      case "PICKED": {
        // casepack
        if (printJob) {
          appLogger.info("Item-Quantity: printjob request", printJob);
          printInstructionPrintJob(printJob);
        } else {
          appLogger.error("Item-Quantity: printjob not found", {});
          console.error("No print job found");
          setAlerts({ error: "No print job found" });
        }
        break;
      }
      case "AVAILABLE": {
        // breakpack
        const trackingId =
          response.data.instruction?.container.trackingId || "";
        appLogger.action("Item-Quantity: navigate to breakpack page");
        if (!enableQtyCorrection) {
          setGlobals({ ...globals, disableQuantityCorrection: false });
        }
        navigate("/breakpack", {
          state: {
            upc,
            splitQty: 0,
            trackingId,
            printJob,
          },
        });
        break;
      }
      default: {
        appLogger.error(
          `Item-Quantity: inventoryStatus ${inventoryStatus} not supported`,
          {}
        );
        console.error("Other types are not supported yet");
        setAlerts({
          error: `Other inventoryStatus ${inventoryStatus} not supported.`,
        });
        break;
      }
    }
    {
      setHeader(<>Barcode scan success</>);
      setContent(<>Receiving successful, label printed</>);
    }
  };

  const receiveItemsError = (error: AxiosError<any>) => {
    setAlerts({
      error:
        error?.response?.data?.errorMessage ||
        error?.message ||
        "Error while receiving",
    });
  };
  const postItemReceiveRequest = (
    receiveInstructionRequest: GetReceiveInstructionRequest
  ) =>
    axiosRequest(apiHandler.getReceiveInstruction(receiveInstructionRequest), {
      description: `Item-Quantity: receive items for upc ${receiveInstructionRequest.upcNumber} delivery ${receiveInstructionRequest.deliveryNumber}`,
    });
  const { mutate: receiveTrustedItems } = useMutation(postItemReceiveRequest, {
    onSuccess: (response) =>
      receiveTrustedItemsSuccess(
        response as AxiosResponse<InstructionResponse>
      ),
    onError: (error) => receiveItemsError(error as AxiosError<any>),
  });

  const receiveForTrustedSeller = useCallback(
    async (deliveryDocs: DeliveryDocument[]) => {
      const gs1 = parsed2dMap.get("gs1") && parsed2dMap.get("gs1");
      const receiveItemRequest = {
        deliveryNumber: `${scanDockTagOrTclNavState?.deliveryNumber}`,
        doorNumber: `${scanDockTagOrTclNavState?.doorNumber}`,
        upcNumber: upc,
        deliveryDocuments: deliveryDocs,
        enteredQty: Number(parsed2dMap.get("30")),
        isMultiSKUItem: globals.multiSku,
        scannedDataList: getScannedDataList(parsed2dMap),
        receivingType: parsed2dMap.size > 0 ? "GS1" : "upc",
        previouslyScannedDataList: (gs1 && [gs1]) || null,
      };
      const isprinterSelected = await printingHandler.isPrinterSelected();
      if (isprinterSelected) {
        // trigger loading
        setAlerts({ loading: true });
        if (enableRegulatedItemChange) {
          receiveTrustedItems(
            Object.assign(receiveItemRequest, {
              isVendorComplianceValidated: true,
            })
          );
        } else {
          receiveTrustedItems(receiveItemRequest);
        }
      } else {
        setAlerts({
          error: "No printer selected, please select a printer to receive",
        });
        appLogger.error(
          "Item-Quantity: No printer selected, please select a printer to receive",
          {}
        );
      }
    },
    [
      receiveTrustedItems,
      setopenRegulatedModal,
      deliveryDoc,
      scanDockTagOrTclNavState,
      upc,
      parsed2dMap,
    ]
  );

  const { refetch } = useQuery(
    ["scanUPC", upc, parsed2dMap],
    () => {
      const gs1 = parsed2dMap.get("gs1") && parsed2dMap.get("gs1");

      setAlerts({ loading: true });
      return enableRegulatedItemChange
        ? postScannedUpcInfo({
            deliveryNumber: isTCLRouteFlow ? "0" : `${scanDockTagOrTclNavState?.deliveryNumber}`,
            doorNumber: `${scanDockTagOrTclNavState?.doorNumber}`,
            upcNumber: isTCLRouteFlow
              ? scanDockTagOrTclNavState?.dockScanUpc
              : parsed2dMap.size > 0
              ? parsed2dMap.get("01") || ""
              : upc,
            previouslyScannedDataList: (gs1 && [gs1]) || null,
            deliveryDocuments: null,
            enteredQty: null,
            isMultiSKUItem: globals.multiSku,
            isVendorComplianceValidated: false,
            scannedDataList:
              parsed2dMap.size > 0 ? getScannedDataList(parsed2dMap) : null,
            receivingType: parsed2dMap.size > 0 ? "GS1" : "UPC",
            ...(enableNewTCLRoute && isTCLRouteFlow && {additionalParams: { po : scanDockTagOrTclNavState?.poNumber}}),
          })
        : getScannedUpcInfo(`${scanDockTagOrTclNavState?.deliveryNumber}`, upc);
    },
    {
      enabled: !!upc,
      onSuccess: enableRegulatedItemChange
        ? getScannedUpcInfoSuccess
        : getScannedUpcInfoSuccessViaGdm,
      onError: enableRegulatedItemChange
        ? getScannedUpcInfoError
        : getScannedUpcInfoErrorViaGdm,
    }
  );

  const getScannedDataList = (parsed2dMap: Map<string, string>) => {
    const scannedDataListArray: ScannedDataListitem[] = [];
    parsed2dMap.forEach((key, value) => {
      if (value !== "gs1") {
        scannedDataListArray.push({
          key: applicationIdentifier.get(value)?.purpose,
          applicationIdentifier: applicationIdentifier.get(value)?.ai,
          value: key,
        } as ScannedDataListitem);
      }
    });
    return scannedDataListArray;
  };

  const onBarcodeScanned = (value: string) => {
    appLogger.action(`Scan-Upc: scanned/enetered ${value} upc`);
    if (upc === value) {
      refetch();
    } else {
      setUpc(value);
    }
  };

  useScannedEventListener((event) => {
    const parsed = event.detail.parsed.get("1D");
    const parsed2dMap = event.detail.parsed.get("2D");
    if (parsed2dMap) {
      if (
        parsed2dMap.get("400") &&
        parsed2dMap.get("01") &&
        parsed2dMap.get("30")
      ) {
        setParsed2dMap(parsed2dMap);
        onBarcodeScanned(parsed2dMap.get("400"));
        if (parsed2dMap.get("gs1")) {
          setGlobals({
            ...globals,
            previouslyScannedDataList: [parsed2dMap.get("gs1")],
          });
        }
      } else {
        setAlerts({
          error: `System is unable to process QR code at the moment. Please scan item UPC to receive.`,
        });
      }
    } else if (parsed && !parsed2dMap) {
      onBarcodeScanned(parsed);
      setParsed2dMap(new Map<string, string>());
    } else {
      setAlerts({
        error: `Unexpected barcode scanned ${event.detail.scanned}`,
      });
    }
  });

  useGlobalKeyboard(
    {
      title: isTCLRouteFlow ? "Enter PO" : "Enter UPC",
      inputLabel: isTCLRouteFlow ? "PO" : "UPC",
      onClickGlobalKeyboardConfirm: (value: string) => onBarcodeScanned(value),
    },
    [upc]
  );

  const onClickExitDockTag = useCallback(() => {
    appLogger.action("Scan-Upc: exit Dock Tag button clicked");
    navigate("/scan-dock-tag-or-tcl", {
      state: {
        cartId: scanDockTagOrTclNavState?.cartId,
        doorNumber: scanDockTagOrTclNavState?.doorNumber,
      },
    });
  }, [navigate, scanDockTagOrTclNavState]);

  const onClickMultiSKU = useCallback(() => {
    appLogger.action("Scan-Upc: exit Dock Tag button clicked");
    setGlobals({ ...globals, multiSku: true });
    navigate("/scan-upc-multisku", {
      state: scanDockTagOrTclNavState,
    });
  }, [setGlobals, globals, navigate, scanDockTagOrTclNavState]);

  const title = enableDockTag
    ? `Dock Tag ${scanDockTagOrTclNavState?.scannedTclorDockTagValue}`
    : `TCL ${scanDockTagOrTclNavState?.scannedTclorDockTagValue}: Scan UPC`;

  const exitButton = enableDockTag ? "Exit Dock Tag" : "Exit TCL";

  return (
    <>
      <SubHeader
        icon={
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            disabled
          >
            <Barcode />
          </IconButton>
        }
        title={title}
        chip={<Chip label={"SINGLE SKU"} color="default" sx={styles.chip} />}
        actions={[
          <Button
            variant="outlined"
            onClick={onClickExitDockTag}
            key="exit dock tag"
            size="large"
            sx={styles.button}
          >
            {exitButton}
          </Button>,
        ]}
      />
      {openRegulatedModal && (
        <RegulatedItemModal
          regulatedItemDeliveryDocLine={
            deliveryDoc ? deliveryDoc[0].deliveryDocumentLines[0] : null
          }
          open={openRegulatedModal}
          onApprove={onApproveRegulatedItemLables}
          onClose={() => {
            setopenRegulatedModal(false);
          }}
        />
      )}
      {openLabelPrintSuccess && (
        <CustomConfirmDialogBox
          getHeader={header}
          getContent={content}
          handleConfirm={onApprovePrintingLabelSuccess}
          open={openLabelPrintSuccess}
        />
      )}
      <Box />
      <BottomImageCard id={`Cart #: ${scanDockTagOrTclNavState?.cartId}`}>
        <Typography variant="h4" component="div" sx={styles.h4}>
          {isTCLRouteFlow ? "Scan PO" : "Scan 2D barcode or item UPC"}
        </Typography>
        <Scan2DBarcode />
        <Typography variant="h6" component="div" sx={styles.h6}>
          Are there multiple SKU numbers for this tag?
        </Typography>
        <Button
          variant="outlined"
          onClick={onClickMultiSKU}
          key="exit dock tag"
          size="large"
          sx={styles.button}
        >
          Multi-SKU
        </Button>
      </BottomImageCard>
    </>
  );
}
