import { memo } from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Typography from "@mui/material/Typography";

import { DeliveryDocumentLine } from "../../types/instruction";
import { MyAppsData } from "../../types/my-apps";
import { sessionHandler } from "../../utilities/session-handler";
import {
  RegulatedImages,
  regulatedItemInfoFromDeliveryDocumentLine,
} from "./regulated-images";
import { styles } from "./regulated-items.styles";

interface RegulatedItemModalProps {
  open: boolean;
  onApprove: () => void;
  onClose: () => void;
  regulatedItemDeliveryDocLine?: DeliveryDocumentLine | null;
}
/**
 * modal to render regulated item function
 */
export const RegulatedItemModal = memo(
  ({
    open,
    onApprove,
    onClose,
    regulatedItemDeliveryDocLine,
  }: RegulatedItemModalProps) => {
    if (!regulatedItemDeliveryDocLine) {
      return null;
    }

    const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
    const enableRegulatedItemChange =
      myAppsData?.dynamicConfig.enableRegulatedItemChange || false;

    const itemDescription1 = enableRegulatedItemChange
      ? regulatedItemDeliveryDocLine.description
      : regulatedItemDeliveryDocLine.itemDescription1;

    const itemUPC = enableRegulatedItemChange
      ? regulatedItemDeliveryDocLine.itemUpc
      : regulatedItemDeliveryDocLine.itemUPC;

    const { text = "", images } = regulatedItemInfoFromDeliveryDocumentLine(
      regulatedItemDeliveryDocLine
    );

    return (
      <Dialog
        open={open}
        onClose={onClose}
        aria-labelledby="regulated-item-modal-title"
        aria-describedby="regulated-item-modal-description"
      >
        <DialogTitle id="alert-dialog-title" sx={styles.dialogText}>
          <Typography variant="h6" component="div" sx={{ fontWeight: "bold" }}>
            {text}
          </Typography>
          <Typography variant="caption" component="div">
            Please confirm all cases include the labels below.
          </Typography>
        </DialogTitle>
        <DialogContent sx={styles.dialogText}>
          <RegulatedImages imageUrls={images} />
          <Box id="alert-dialog-description" sx={{ color: "white" }}>
            <Typography variant="h6" sx={styles.itemDescription}>
              {itemDescription1}
            </Typography>
            <Typography gutterBottom variant="body2">
              {`UPC: ${itemUPC}`}
            </Typography>
          </Box>
        </DialogContent>
        <Box sx={styles.info}>
          Please apply this label or confirm the label is applied.
        </Box>
        <DialogActions>
          <Button variant="contained" sx={styles.button} onClick={onApprove}>
            Continue
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
);
RegulatedItemModal.displayName = "RegulatedItemModal";
